.App {
  text-align: center;
  margin: 20%;
}


#date{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

}

label{
  display: flex;
  flex-direction: column;
  align-items: center;
}

label > div{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

form{
  background-color: white;
  border-radius: 10px 10px 120px 10px;
}

#resolve-image{
  width: 10%;
  height: 10%;
  float: right;
  cursor: pointer;
}

input{

  width: 60%;
  height: 30px;
  border-radius: 5px;
  text-align: center;
  margin-left: 0 auto;
}

#results{
 padding-bottom: 80px ;
 display: flex;
 flex-direction: column;
 font-size: 2.5em;
 text-align: left;
 
}

#results>h1{
  color: black;
  margin-bottom: -70px;
  font-weight: bolder;
  
}
h1>span{
  color: purple;
  font-weight: bolder;
}

fieldset{
  border: none;
}

span.error{
  color: red;
  font-style: italic;
  display: block;
}

@media(max-width: 480px){
#results{
  font-size:x-small;
  margin-top: 50px;
}

#date{
  grid-template-columns: repeat(3,1fr);
  gap: 10px;
}

label > div {
  width: 100%;
}

#resolve-image{
  width: 20%;
  height: 20%;
  float: center;
}




}

@media(min-width: 481px) and (max-width: 768px){
#results{
  font-size: medium;
  
}
#results>h1{
  color: black;
  margin-bottom: 20px;
  font-weight: bolder;
  
}


}

